import * as React from 'react';
import { css } from 'office-ui-fabric-react';

import styles from './MobilePreviewClickStopBar.module.scss';
import {
  DeviceOrientation,
  DeviceType,
  IDevice
} from '../mobilePreview/MobilePreview';
import MobilePreviewClickStop from '../mobilePreviewClickStop/MobilePreviewClickStop';

export interface IMobilePreviewClickStopBarProps {
  currentDevice: IDevice;
  currentOrientation: DeviceOrientation;
  devices: IDevice[];
  deviceType: DeviceType;
  onSelectClickStop: (device: IDevice) => void;
}

export default class MobilePreviewClickStopBar extends React.Component<IMobilePreviewClickStopBarProps, void> {

  public render(): React.ReactElement<IMobilePreviewClickStopBarProps> {
    const props: IMobilePreviewClickStopBarProps = this.props;
    const deviceDiv: React.ReactElement<MobilePreviewClickStop>[] = [];

    for (const device of props.devices) {
      let selected: boolean = false;
      if (device === props.currentDevice) {
        selected = true;
      }
      deviceDiv.push(
        <MobilePreviewClickStop
          key={ device.name }
          device={ device }
          onClick={ () => props.onSelectClickStop(device) }
          selected={ selected }
          orientation={ props.currentOrientation }
          deviceType={ this.props.deviceType }
        />
      );
    }

    const containerStyle: string = css(styles.clickStopNavBar, 'ms-borderColor-neutralTertiaryAlt',
      'ms-bgColor-neutralLighter', 'ms-fontColor-neutralPrimary');

    return (
      <div className={ containerStyle }>
        <ul className={ styles.navBarItems }>
          { deviceDiv }
          <div id='clickStopBarLabel' className={ styles.mobilePreviewDeviceTitle }>{ props.currentDevice.name }</div>
        </ul>
      </div>
    );
  }

}
